import { getDefaultSettings } from "http2"
import moment from "moment"
import React, { useEffect } from "react"
import { Person } from "../types/Person"

export default function useTimer(initialState: number, friendlyName: string): [number, Person, boolean, boolean, () => void, () => void, () => void, () => void] {

  const [timer, setTimer] = React.useState(initialState)
  const [person, setPerson] = React.useState(getDefaultSettings())
  const [isActive, setIsActive] = React.useState(false)
  const [isPaused, setIsPaused] = React.useState(false)
  const countRef = React.useRef<NodeJS.Timeout>()


  function getDefaultSettings() {
    let defaultPerson = 
    {
      friendlyName: friendlyName,
      today: '', totalTimeSpent: 0, totalTimeSpentToday: 0
    } as Person
    let personStr = localStorage.getItem(friendlyName)
     if (personStr != null) {
       return JSON.parse(personStr) as Person;
    } else {
      return defaultPerson;
    }

  }

  const handleStart = () => {
    setIsActive(true)
    setIsPaused(true)
    countRef.current = setInterval(() => {
      setTimer((timer) => timer + 1)
    }, 1000)
  }

  const handlePause = () => {
    clearInterval(countRef.current as NodeJS.Timeout)
    setIsPaused(false)
  }

  const handleResume = () => {
    setIsPaused(true)
    countRef.current = setInterval(() => {
      setTimer((timer) => timer + 1)
    }, 1000)
  }

  function updateStorage(key: string = '') {
    let personStr = localStorage.getItem(key)
    if (personStr != null) {
      setPerson(JSON.parse(personStr) as Person);
    }
    let day: string = moment().format('dddd').toLowerCase();
    if (day != person.today) {
      person.today = day;
      person.totalTimeSpentToday = timer;

    } else {
      person.totalTimeSpentToday += timer;
    }
    person.totalTimeSpent = person.totalTimeSpent == 0 ? timer : person.totalTimeSpent + timer;
    localStorage.setItem(friendlyName, JSON.stringify(person));
    setPerson(person)
  }

  const handleReset = () => {
    updateStorage(friendlyName)

    clearInterval(countRef.current as NodeJS.Timeout)
    setIsActive(false)
    setIsPaused(false)
    setTimer(0)
  }

  return [timer, person, isActive, isPaused, handleStart, handlePause, handleResume, handleReset]
}