import { useState } from "react";
import { useInterval } from "usehooks-ts";
import { StopPointArrival } from "../clients/tfl";

export default function useBusTimes(stopPointId: string, getStopPointArrivals: (stopPointId: string) => Promise<Array<StopPointArrival>>): [Array<StopPointArrival> | undefined, boolean] {
    const [stopPointArrivals, setStopPointArrivals] = useState<Array<StopPointArrival> | undefined>();
    const [loading, setLoading] = useState<boolean>(true);

    useInterval(() => {
        getStopPointArrivals(stopPointId).then(response => {
            setStopPointArrivals(response)
        }).finally(() => {
            setLoading(false)
        })

        return () => {
            setStopPointArrivals(undefined)
        }
    }, 10000)

    return [stopPointArrivals, loading]

}