import { Box, Button, Grid, LinearProgress, Paper, Theme, Typography } from "@mui/material";
import createStyles from '@mui/styles/createStyles';
import makeStyles from '@mui/styles/makeStyles';
import moment from "moment";
import { StopPointArrival } from "../clients/tfl";
import useBusTimes from "./useBusTimes";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({

        root: {
            flexGrow: 1,
        },
        paper: {
            padding: theme.spacing(2),
            textAlign: 'center',
            color: theme.palette.text.secondary,
        },
        button: {
            padding: theme.spacing(2),
            textAlign: 'center',
        },
    }),
);
interface Props {
    stopPointId: string
    getStopPointArrivals: (stopPointId: string) => Promise<Array<StopPointArrival>>
}

export default function Bus(props: Props) {
    const [stopPointArrivals, loading] = useBusTimes(props.stopPointId, props.getStopPointArrivals)
    const classes = useStyles();

    return <>

        {stopPointArrivals === undefined || stopPointArrivals?.length === 0 ?
            <Box>
                <LinearProgress></LinearProgress>
            </Box>
            :
            stopPointArrivals?.map((stopPoint) => (
                <Box key={stopPoint.timeToStation}>
                    <Typography variant="body1">{`${stopPoint.lineName} - ${stopPoint.destinationName} - ${moment.duration(stopPoint.timeToStation, 'seconds').minutes()} mins`}</Typography>
                </Box>
            ))}
    </>

}