import superagent from "superagent"

export interface StopPointArrival {
    vehicleId: string
    lineName: string
    destinationName: string
    timeToStation: number
}

export function getStopPointArrivals(): (stopPointId: string) => Promise<Array<StopPointArrival>> {
    return (stopPointId) => superagent
    .get(`https://api.tfl.gov.uk/StopPoint/${stopPointId}/arrivals`)
    .then((response) => response.body as Array<StopPointArrival>)

}