import { Box, Button, Grid, Paper, Theme, Typography } from "@mui/material";
import createStyles from '@mui/styles/createStyles';
import makeStyles from '@mui/styles/makeStyles';
import { Person } from "../types/Person";
import useTimer from "./useTimer";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({

        root: {
            flexGrow: 1,
        },
        paper: {
            padding: theme.spacing(2),
            textAlign: 'center',
            color: theme.palette.text.secondary,
        },
        button: {
            padding: theme.spacing(2),
            textAlign: 'center',
        },
    }),
);
const formatTime = (timer: number) => {
    const getSeconds = `0${(timer % 60)}`.slice(-2)
    const minutes = Math.floor(timer / 60)
    const getMinutes = `0${minutes % 60}`.slice(-2)
    const getHours = `0${Math.floor(timer / 3600)}`.slice(-2)

    return `${getHours} : ${getMinutes} : ${getSeconds}`
}

interface Props {
    friendlyName: string
}

export default function Timer(props: Props) {
    const [timer, person, isActive, isPaused, start, pause, resume, reset] = useTimer(0, props.friendlyName)
    const classes = useStyles();

    return <>
        <Box>
            <Typography variant="h3">{formatTime(timer)}</Typography>
            <Typography variant="h3">{formatTime(person.totalTimeSpentToday)}</Typography>
            <Typography variant="h3">{formatTime(person.totalTimeSpent)}</Typography>
            <Grid container item xs={12} spacing={1}>
                <Grid item xs={6}>
                    {!isActive && !isPaused ?
                    <Button onClick={() => { start() }} className={classes.button} variant="outlined" color="primary">START</Button>
                    :
                    ( isPaused ? <Button onClick={() => { pause() }} className={classes.button} variant="outlined" color="primary">PAUSE</Button>
                    :
                    <Button onClick={() => { resume() }} className={classes.button} variant="outlined" color="primary">RESUME</Button>)}

                </Grid>

                <Grid item xs={6}>
                    <Button onClick={() => { reset()}} disabled={!isActive} className={classes.button} variant="outlined" color="primary">RESET</Button>
                </Grid>
            </Grid>
        </Box>
    </>

}